@import "../abstracts/text-styles";
@import "../abstracts/color-styles";

.backstage {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    // width: 72.5%;
    width: 1160px;
    height: 403px;
    margin: 140px auto 180px;
    .photo {
        background-image: url(../assets/images/bamboo-cap.jpg);
        background-repeat: no-repeat;
        grid-area: 1 / 1 / 2 / 2;
        width: 105%;
        height: 99%;
    }
    .text {
        grid-area: 1 / 2 / 2 / 3;
        padding-left: 20%;
        color: $color-black-blue;
        h2 {
            @include h2;
            margin: 0;
        }
        h3 {
            @include h3;
            margin: 0 0 32px;
        }
        p {
            @include p;
        }
        .one {
            margin: 22px 0 55px;
        }
        .two {
            margin: 0;
            line-height: 140%;
        }
    }
    .flower {
        position: absolute;
        width: 204px;
        height: 605px;
        right: 0%;
        top: 792px;
        background-image: url(../assets/images/flower_foto1.png);
        background-repeat: no-repeat;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    }
}
